// Prism

/* Code blocks */
pre[class*="language-"] {
  padding: 1em;
  overflow: auto;
  margin-bottom: 0px;
  border-radius: 0.5rem !important;
}



div.code-toolbar {
  position: relative;
  > .toolbar {
    position: absolute;
    top: .3em;
    right: .2em;
    transition: opacity 0.3s ease-in-out;
    opacity: 0;
  }
  &:hover > .toolbar, &:focus-within > .toolbar {
    opacity: 1;
  }
  > .toolbar > .toolbar-item {
    display: inline-block;
    > {
      a {
        cursor: pointer;
      }
      button {
        background: none;
        border: 0;
        color: var(--#{$prefix}gray-900);
        font: inherit;
        line-height: normal;
        overflow: visible;
        padding: 0;
      }
      a, button, span {
        color: var(--#{$prefix}gray-900);
        font-size: .75rem;
        padding: .25rem .5rem;
        background: var(--#{$prefix}gray-300);
      font-weight: 600;
        border-radius: .25rem;
      }
      a {
        &:hover, &:focus {
          color: var(--#{$prefix}gray-900);
          text-decoration: none;
        }
      }
      button {
        &:hover, &:focus {
          color: var(--#{$prefix}gray-900);
          text-decoration: none;
        }
      }
      span {
        &:hover, &:focus {
          color: inherit;
          text-decoration: none;
        }
      }
    }
  }
}

